.classes-page {
  padding: 0;
  background: linear-gradient(135deg, #FFF5E6 0%, #FFEBCD 100%);
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
}

/* Animated background elements */
.classes-page::before {
  content: '';
  position: absolute;
  top: -10%;
  right: -5%;
  width: 30%;
  height: 30%;
  background: rgba(210, 105, 30, 0.05);
  border-radius: 50%;
  animation: float 8s ease-in-out infinite;
}

.classes-page::after {
  content: '';
  position: absolute;
  bottom: -10%;
  left: -5%;
  width: 25%;
  height: 25%;
  background: rgba(139, 69, 19, 0.05);
  border-radius: 50%;
  animation: float 10s ease-in-out infinite reverse;
}

.classes-section {
  width: 95vw;
  height: 90vh;
  margin: 0;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  z-index: 1;
  margin-top: 100px;
}

.classes-section h2 {
  color: #8B4513;
  text-align: center;
  font-size: 2.5rem;
  margin: 0;
  font-family: 'Playfair Display', serif;
  position: relative;
  animation: fadeInDown 0.8s ease-out;
  text-shadow: 1px 1px 3px rgba(139, 69, 19, 0.2);
}

.classes-section h2:after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 2px;
  background: #8B4513;
  animation: scaleIn 0.8s ease-out;
}

.mode-selector {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 0.5rem 0;
  animation: fadeIn 0.8s ease-out;
}

.mode-button {
  padding: 0.6rem 1.8rem;
  border: 1.5px solid #8B4513;
  background: transparent;
  color: #8B4513;
  font-size: 1rem;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  animation: popIn 0.5s ease-out;
}

.mode-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 3px 10px rgba(139, 69, 19, 0.2);
}

.mode-button.active {
  background: #8B4513;
  color: #FFF;
  font-weight: 500;
}

.categories-container {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  width: 100%;
  height: 65%;
  padding: 0.5rem;
  animation: fadeIn 1s ease-out;
}

.class-category {
  width: 30%;
  min-width: 300px;
  max-width: 400px;
  background: rgba(255, 255, 255, 0.97);
  border-radius: 15px;
  padding: 1.5rem;
  box-shadow: 0 8px 25px rgba(139, 69, 19, 0.1);
  height: 100%;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  animation: floatUp 0.8s ease-out;
  display: flex;
  flex-direction: column;
}

.class-category:nth-child(1) { animation-delay: 0.1s; }
.class-category:nth-child(2) { animation-delay: 0.2s; }
.class-category:nth-child(3) { animation-delay: 0.3s; }

.class-category:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 30px rgba(139, 69, 19, 0.15);
}

.category-title {
  color: #8B4513;
  font-size: 1.7rem;
  margin-bottom: 1rem;
  text-align: center;
  font-family: 'Playfair Display', serif;
  position: relative;
  animation: fadeIn 0.8s ease-out;
}

.category-title:after {
  content: '';
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
  width: 35px;
  height: 2px;
  background: #D2691E;
  animation: scaleIn 0.8s ease-out;
}

.class-card {
  border-radius: 12px;
  padding: 1.5rem;
  background: white;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 4px 15px rgba(139, 69, 19, 0.08);
  transition: all 0.4s ease;
}

.class-card:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 20px rgba(139, 69, 19, 0.15);
}

.class-card h3 {
  color: #8B4513;
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 1rem;
  font-family: 'Playfair Display', serif;
  position: relative;
}

.class-card h3::after {
  content: '';
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
  width: 25px;
  height: 2px;
  background: #D2691E;
}

.fee-details {
  color: #6B4423;
  text-align: center;
  line-height: 1.6;
}

.fee-details p {
  margin: 0.5rem 0;
  font-size: 1.1rem;
  animation: fadeIn 1s ease-out;
}

.fee-details .registration {
  color: #D2691E;
  font-weight: 600;
  font-size: 1.2rem;
  margin: 0.8rem 0;
  animation: pulse 2s infinite;
}

.fee-details small {
  display: block;
  margin-top: 0.8rem;
  color: #8B4513;
  font-style: italic;
  font-size: 0.85rem;
}

.enroll-button.main {
  display: block;
  margin: 0.8rem auto 0;
  padding: 0.6rem 1.8rem;
  background: transparent;
  color: #8B4513;
  border: 1px solid #8B4513;
  border-radius: 25px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  animation: fadeInUp 1s ease-out;
}

.enroll-button.main:hover {
  background: #8B4513;
  color: #FFF;
  transform: translateY(-2px);
}
.group-notice {
  margin-top: 1rem;
  padding: 0.75rem;
  text-align: center;
  color: #8B4513;
  background: linear-gradient(145deg, #fff5e6, #fff9f0);
  border-radius: 8px;
  box-shadow: 0 2px 10px rgba(139, 69, 19, 0.1);
  font-style: italic;
  animation: shine 2s infinite;
}
.sticker-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.sticker-image {
  width: 140px;
  height: 140px;
  object-fit: contain;
  border-radius: 12px;
  animation: smoothBounce 3s ease-in-out infinite;
    transition: transform 0.3s ease;
}

.sticker-image:hover {
  transform: scale(1.1) rotate(5deg);
}
@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes shine {
  0% { opacity: 0.8; }
  50% { opacity: 1; }
  100% { opacity: 0.8; }
}
/* Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    transform: translateX(-50%) scaleX(0);
  }
  to {
    transform: translateX(-50%) scaleX(1);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-15px) rotate(2deg);
  }
}

@keyframes floatUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes popIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.03);
    opacity: 0.9;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Responsive adjustments */
@media (max-width: 1024px) {

  .classes-page {
    overflow-y: auto; /* Enable vertical scrolling */
    height: auto; /* Let content determine height */
    min-height: 100vh; /* At least viewport height */
  }
  
  .classes-section {
    height: auto; /* Let content determine height */
    min-height: 100vh; /* At least viewport height */
    padding-bottom: 2rem; /* Add some space at bottom */
  }
  .classes-section h2 {
    font-size: 2.2rem;
  }
  
  .categories-container {
    gap: 1rem;
  }
  
  .class-category {
    width: 32%;
    min-width: 250px;
    padding: 1.2rem;
  }
}

@media (max-width: 768px) {
  .classes-section {
    height: auto;
    min-height: 100vh;
    padding: 100px 0.5rem 2rem;
    margin-top: 0;
  }
  
  .classes-page {
    position: relative;  /* Change to relative */
    overflow-y: auto;   /* Enable vertical scrolling */
    min-height: 100vh;
    height: auto;
  }
  
  .classes-section h2 {
    font-size: 2rem;
    margin-bottom: 0.5rem;
  }
  
  .categories-container {
    flex-wrap: wrap;
    height: auto;
    padding-bottom: 2rem;
    align-content: center;
  }
  
  .class-category {
    width: 95%;
    height: 48%;
    min-width: 200px;
  }
}

@media (max-width: 480px) {
  .classes-page {
    overflow-x: hidden;  /* Prevent horizontal scrolling */
  }

  .classes-section h2 {
    font-size: 1.8rem;
  }

  .classes-section {
    padding: 80px 0.5rem 2rem;
    height: auto;
  }
  
  .class-category {
    min-height: auto; /* Remove fixed height */
    height: auto; /* Let content determine height */
    margin-bottom: 1.5rem; /* Add space between stacked cards */
    width: 90%;
  }

  .mode-selector {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .categories-container {
    height: 75%;
    gap: 1.5rem;
  }
  
  .category-title {
    font-size: 1.5rem;
  }
  
  .class-card h3 {
    font-size: 1.3rem;
  }
  .sticker-image {
    width: 100px;  /* Reduced size for smaller screens */
    height: 100px;
  }
}